import LinearDeterminate from './Linearspinner';
import CircularIndeterminate from './Circularspinner';

function Result({image, error, loading, imageDone, imageError, loaded, nextImage, nextNextImage}){
    console.log(error)
    return(
        <>
        {(error) && <p>Can't display image, please try again...</p>}
        <a href={image} target='_blank'>
        <img
            style={{display: !loading && image? 'block': 'none'}} 
            src={image} 
            onLoad={imageDone} 
            onError={imageError} 
            alt=''
        />
        </a>
        {loading && image!==undefined && <CircularIndeterminate />}
    
        <img
            style={{display: 'none'}} 
            src={nextImage} 
            alt=''
        />
        
        <img
            style={{display: 'none'}} 
            src={nextNextImage} 
            alt=''
        />
        
        </>
    );
}

function ResultDisplay(props){
    return(
        <div className='top-level-display'>
            <div className='display-result'>
                <Result {...props}/>
                <br></br>
            </div>
            <div className='buttons'>
                {props.image && props.k>0 ? <button style={{float: 'left'}} onClick={props.previous}>Previous</button> : ''}
                {props.image && props.k<9 ? <button style={{float: 'right'}}  onClick={props.next}>Next</button> : <button style={{float: 'right'}}  onClick={props.new}>Next</button>}
            </div>
        </div>
    )
}

export default ResultDisplay;