import React, { useState } from 'react'

function Search({onSubmit, onInputChange, buttonDisabled}){
    
    return(
        <div className='search'>
            <input placeholder='Search here' type='text' onChange={onInputChange}/>
            <button disabled={buttonDisabled} type='submit' onClick={onSubmit}>Search</button>
        </div>
    )
}

export default Search;