import React, { useState, useEffect } from 'react';

import fetcher from './components/fetcher';
import Header from './components/header';
import Search from './components/searchbar';
import ResultDisplay from './components/Resultdisplay';
import Footer from './components/footer';

import './styles/main.css'

const imageChooser = (all, index) => all[index];

function App(){
  const [image, setImage] = useState(null);
  const [input,  setInput] = useState('');
  const [loading, setLoading] = useState(false);
  const [error,  setError] = useState(false);
  const [i, setI] = useState(0);
  const [allPics, setAllPics] = useState([]);
  const [loaded, setLoaded] = useState(false);

  const handleChange = event => setInput(event.target.value);

  const imageChanger = ()=> {
    setI(0);
    console.log(i)
    setLoading(true);
    setError(false);
    fetcher(input)
      .then(res => {
        if (res==='error'){
          setLoading(false);
          setAllPics(null);
          setError(true);
          console.log('first err');
        }else{
          setAllPics(res);
          
          setAllPics((state)=>{
            var img = imageChooser(state, i);
            setImage(img);
            console.log(img)
            return state
          })
          console.log('1')
        }
        
      })
      .catch(err =>{
        setImage(null);
        setLoading(false);
        setError(true);
        console.log(err);
        console.log('second err');
      })
  }
  
  const nextImage = ()=>{
    setLoading(true);
    setImage(null);
    setI(i+1)
    setI((state)=>{
      console.log(state)
      var img = imageChooser(allPics, state);
      setImage(img)
      console.log('2')
      return state
    })
    
  }

  const previousImage = ()=>{
    setLoading(true);
    setImage(null);
    setI(i-1)
    setI((state)=>{
      console.log(state)
      var img = imageChooser(allPics, state);
      setImage(img)
      console.log(3)
      return state
    })
  }

  const imageLoaded =()=> {
    setLoaded(true);
    setLoading(false);
    setError(false);
  }

  const imageError =()=>{
    setError(true);
    console.log('third err');
    setLoading(false);
    setImage(null);
  }

  return(
    <>
      <Header />
      <Search buttonDisabled={input===''} onInputChange={handleChange} onSubmit={imageChanger}/>
      
      <ResultDisplay
        nextImage={allPics? allPics[i+1]: setError(true)}
        nextNextImage={allPics? allPics[i+2]: setError(true)}  
        loaded={loaded} 
        imageError={imageError} 
        imageDone={imageLoaded} 
        k={i} 
        next={()=> nextImage()}
        previous={()=> previousImage()} 
        image={image} 
        error={error} 
        loading={loading}
        new={imageChanger}
      />
      
      {/* <Footer />  */}
    </>
  )
}

export default App;